/**
 * 
 */
class MgrGlobalCtxPos {
    /**
     *  x
     */
    clientX = 0;

    /**
     *  y
     */
    clientY = 0;

    /**
     * 
     * @param clientX 
     * @param clientY 
     */
    fill (clientX: number, clientY: number) {
        this.clientX = clientX;
        this.clientY = clientY;
    }
}

export default MgrGlobalCtxPos;