import JWebglProgram from "./JWebglProgram";

/**
 * 
 */
export default class JWebglProgramVarying {
    /**
     * 
     */
    relProgram: JWebglProgram;

    /**
     * 
     */
    name: string;

    constructor (
        program: JWebglProgram,
        name: string
    )
    {
        this.relProgram = program;
        this.name = name;
    }

    /**
     *  shader 
     * @returns 
     */
    impGetShaderDefine (): string {
        return null;
    }

    /**
     *  shader
     * @returns 
     */
    toString () {
        return this.name;
    }
}