/**
 *  - 
 */
export default class ObjectPoolType <T> {
    /**
     * 
     */
    _coll = new Array <T> ();

    /**
     * 
     */
    _set = new Set <T> ();

    /**
     * 
     */
    instantiate: () => T;

    /**
     *  - 
     */
    onPop: (t: T) => void;

    /**
     *  - 
     */
    onPush: (t: T) => void;

    constructor (args: {
        instantiate: () => T,
        onPop: (t: T) => void,
        onPush: (t: T) => void
    })
    {
        this.instantiate = args.instantiate;
        this.onPop = args.onPop;
        this.onPush = args.onPush;
    }
}