import NodeModules from "../NodeModules";
import ReactComponentExtend from "../common/ReactComponentExtend";
import DomRoot from "../ui/DomRoot";

/**
 * 
 */
class MgrDom {
    /**
     * 
     */
    private _root;

    /**
     * 
     * @returns 
     */
    init () {
        this._root = NodeModules.reactDomClient.createRoot (document.getElementById('app'));
        return Promise.resolve ();
    }

    /**
     * 
     */
    version = 0;

    /**
     * 
     */
    refresh () {
        this.version++;
        // 
        this._root.render(
            ReactComponentExtend.instantiateComponent (DomRoot, null)
        );
    }
}

namespace MgrDom {
    /**
     * 
     */
    export const inst = new MgrDom ();
};

export default MgrDom;